import React, { useContext } from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import { Header } from './component/Header';
import { SiderDrawer } from './component/SiderDrawer';
import { useStyles } from './SidebarStyle';
import HeaderContext, { HeaderContextProp } from '../../layout/HeaderContext';
import Button from '@material-ui/core/Button';

interface Props {
  children?: JSX.Element[] | JSX.Element
}

// export default function Sidebar() {
export const Sidebar: React.SFC<Props> = (props) => {

  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };


  const { headerVisible, setHeaderVisible } = useContext<HeaderContextProp>(HeaderContext)

  return (

    <div className={classes.root}>
      {/* <CssBaseline /> */}

      {headerVisible ?
        <>
          <Header classes={classes} handleDrawerOpen={handleDrawerOpen} open={open} />

          <SiderDrawer classes={classes} open={open} handleDrawerClose={handleDrawerClose} />
        </>
        :
        null
      }

      <div className={headerVisible ? classes.content : ""}>
        {props.children}
      </div>
    </div>
  );
}